import React, {Component, useState} from 'react';
import {
    AppBar,
    Badge,
    Box,
    Button,
    Container,
    CssBaseline,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Grow,
    IconButton, Snackbar,
    TextField,
    Toolbar,
    Tooltip,
    Typography
} from '@material-ui/core';
import {random, sample} from "lodash";

import realData from "./real.json";
import fakeData from "./fake.json";
import {
    ArrowForward,
    Cancel,
    Check,
    Clear,
    Close,
    Help,
    Report,
    ReportOutlined,
    ReportProblem,
    ReportProblemOutlined,
    Share,
    WhatsApp
} from "@material-ui/icons";
import {SpeedDial, SpeedDialAction, SpeedDialIcon} from "@material-ui/lab";
import {
    FacebookIcon,
    FacebookShareButton,
    TwitterShareButton,
    TwitterIcon,
    FacebookMessengerIcon,
    FacebookMessengerShareButton,
    WhatsappIcon,
    WhatsappShareButton
} from "react-share";
import {homepage} from '../package.json';


function Choice(props) {
    const color = props.isOver ? (props.showCorrect ? (props.isCorrect ? "#1B5E20" : "#b71c1c") : "#9E9E9E") : "#212121";

    function searchInNewTab(text) {
        const url = `http://www.google.com/search?q="${text}"+site:wiadomosci.tvp.pl`;
        window.open(url, "_blank");
    }

    return <Box display={"flex"}>
        <Box style={{
            width: props.isOver ? 32 : 0,
            transition: "width 150ms ease-in-out"
        }}>
            {props.isOver && <Grow in={true} style={{marginTop: ".275em"}}>
                {props.isCorrect ?
                    <Check style={{color: color}}/> :
                    <Clear style={{color: color}}/>}
            </Grow>}
        </Box>
        <Box flexGrow={1}>
            <Button size={"large"} style={{width: "100%", color: color}}
                    onClick={() => {
                        props.isOver ? searchInNewTab(props.text) : props.onClick()
                    }}>{props.text}</Button>
        </Box>
    </Box>
}


function Level(props) {
    const n = 4;
    const [correctChoice,] = useState(random(0, n - 1));

    function randomChoices() {
        const choices = [];
        for (let i = 0; i < n; ++i) choices.push(sample(i === correctChoice ? realData : fakeData));
        return choices;
    }

    const [choices,] = useState(randomChoices());
    const [isOver, setIsOver] = useState(false);
    const [clickedChoice, setClickedChoice] = useState(null);

    return <Grow in={true}>
        <Box>
            <Typography>
                Który pasek pojawił się
                w&nbsp;<em>Wiadomościach?</em><br/> Wymagana
                jest <em>pełna</em> zgodność z&nbsp;oryginałem.
            </Typography>
            <Box mt={2}>
                {choices.map((option, i) =>
                    <Box key={i}>
                        <Choice text={option}
                                isCorrect={i === correctChoice}
                                showCorrect={i === correctChoice || i === clickedChoice}
                                isOver={isOver}
                                onClick={() => {
                                    setIsOver(true);
                                    setClickedChoice(i);
                                    props.onAnswer(i === correctChoice);
                                }}/></Box>)}
            </Box>
            <Box mt={2} display={"flex"} justifyContent={"space-between"}>
                <Grow in={isOver}>
                    <Box/>
                </Grow>
                <Grow in={isOver} style={{marginLeft: 16}}>
                    <Tooltip
                        title={"Kliknij żeby przejść dalej, lub kliknij którąś z odpowiedzi żeby ją wyszukać w internecie."}>
                        <Button variant={"contained"} size={"large"}
                                color={"primary"}
                                m={1} endIcon={<ArrowForward/>}
                                onClick={props.onNext}>
                            Dalej
                        </Button>
                    </Tooltip>
                </Grow>
            </Box>
        </Box>
    </Grow>;
}


function SocialButtons() {
    const [open, setOpen] = useState()
    // https://github.com/nygardk/react-share#api
    return <SpeedDial open={open} onOpen={() => setOpen(true)}
                      onClose={() => setOpen(false)} ariaLabel={"udostępnij"}
                      icon={<SpeedDialIcon icon={<Share/>}
                                           openIcon={<Close/>}/>}
                      style={{position: "absolute", bottom: 16, right: 16}}>
        <SpeedDialAction title={"Udostępnij na Twitterze"}
                         icon={<TwitterShareButton
                             url={homepage}><TwitterIcon round size={43}
                                                         style={{marginTop: 6}}/></TwitterShareButton>}/>
        <SpeedDialAction title={"Udostępnij na Facebooku"}
                         icon={<FacebookShareButton url={homepage}><FacebookIcon
                             round size={43}
                             style={{marginTop: 6}}/></FacebookShareButton>}/>
        {/*<SpeedDialAction title={"Udostępnij przez Messengera"}*/}
        {/*                 icon={<FacebookMessengerShareButton*/}
        {/*                     url={homepage}><FacebookMessengerIcon*/}
        {/*                     round size={43}*/}
        {/*                     style={{marginTop: 6}}/></FacebookMessengerShareButton>}/>*/}
        <SpeedDialAction title={"Udostępnij przez WhatsAppa"}
                         icon={<WhatsappShareButton
                             url={homepage}><WhatsappIcon round size={43}
                                                          style={{marginTop: 6}}/></WhatsappShareButton>}/>
    </SpeedDial>;
}

export default class App extends Component {
    constructor(props) {
        super(props);

        this.state = {
            level: 0,
            correct: 0,
            incorrect: 0,
            showHelp: false
        }
    }

    render() {
        return <Container style={{maxWidth: 700}}>
            {/*<CssBaseline/>*/}
            <Box mt={2} mb={2} display={"flex"}
                 justifyContent={"flex-end"}>
                <Box>
                    <Tooltip
                        title={`Poprawne odpowiedzi: ${this.state.correct}`}>
                        <Badge badgeContent={this.state.correct}
                               showZero={true}
                               overlap={"circle"} color={"primary"}>
                            <Check fontSize={"large"} color={"primary"}/>
                        </Badge>
                    </Tooltip>
                </Box>
                <Box>
                    <Tooltip
                        title={`Niepoprawne odpowiedzi: ${this.state.incorrect}`}>
                        <Badge
                            badgeContent={this.state.incorrect}
                            showZero={true} overlap={"circle"}
                            color={"primary"}>
                            <Clear fontSize={"large"} color={"primary"}/>
                        </Badge>
                    </Tooltip>
                </Box>
            </Box>
            <Level key={this.state.level} over={this.state.over}
                   onAnswer={(ok) => this.setState({
                       correct: this.state.correct + (ok ? 1 : 0),
                       incorrect: this.state.incorrect + (ok ? 0 : 1)
                   })}
                   onNext={() => this.setState({level: this.state.level + 1})}/>
            <SocialButtons/>
        </Container>
    }
}
